<template>
    <div class="home">
        <cjcNav title="课表查询" @customRouter="customRouter">
            <Dropdown trigger="click" style="margin-right: 25px; font-size:14px">
                <a href="javascript:void(0)">
                    <span v-if="zc !== '0'">第{{zc}}周</span>
                    <span v-else>选择周次</span>
                    <Icon type="arrow-down-b"></Icon>
                </a>
                <Dropdown-menu slot="list" class="dropdown-text">
                    <Dropdown-item v-for="item in State.TimeList" 
                    :key="item.XN + item.XQ + item.ZC"
                    @click.native="selectZC(item.ZC)">{{'第' + item.ZC + '周'}}</Dropdown-item>
                </Dropdown-menu>
            </Dropdown>
        </cjcNav>
        <classSchedule :spanShow="spanShow"/>
    </div>
</template>

<script>

import Modal from 'iview/src/components/modal'
import Message from 'iview/src/components/message'
import Dropdown from 'iview/src/components/dropdown'
export default {
    name: 'home',
    components: {
        cjcNav: () => import('../components/common/Nav.vue'),
        classSchedule: () => import('../components/classSchedule.vue'),
        Dropdown,
        DropdownItem: Dropdown.Item,
        DropdownMenu: Dropdown.Menu,
        Icon: () => import('iview/src/components/icon')
    },
    data() {
        return {
            State: this.$store.state,
            zc: sessionStorage.getItem('ZC'),
            spanShow: false
        }
    },
    props: ['oldRouter'],
    methods: {
        /**
         * 选择周次
         */
        selectZC(ZC) {
            const Vm = this
            let State = Vm.$store.state
            Vm.zc = ZC
            Vm.spanShow = true
            sessionStorage.setItem('ZC', ZC)
            /**
             * 设置课程列表
             */
            async function setSyllabusList() {
                let SyllabusList = await Vm.$store.dispatch('getSyllabusList', {
                    xh: State.userInfo.XH,
                    xn: State.SchoolTime.XN,
                    xq: State.SchoolTime.XQ,
                    zc: ZC
                })
                Vm.spanShow = false
                if (SyllabusList.status === 0) {
                    Vm.$store.commit('SET_SyllabusList', SyllabusList.result)
                } else {
                    Vm.$store.commit('SET_SyllabusList', 'noData')
                    Message.warning(SyllabusList.msg)
                }
            }
            setSyllabusList()
        },
        customRouter() {
            let isMobile = this.$cjc.browser.versions.mobile
            const Vm = this
            if (isMobile) {
                Vm.$cjc.closeWebView()
            } else {
                Vm.$router.back()
            }
        }
    },
    created() {
        Message.config({
            top: 200,
            duration: 3
        })
        let Vm = this
        let State = Vm.$store.state
        let keyId = sessionStorage.getItem('key_id')
        let type = sessionStorage.getItem('type')
        // Vm.$route.params = Object.assign(Vm.$route.params, Vm.$route.query)
        /**
         * 确认用户信息,并保存用户信息
         */
        async function setUserInfo() {
            // let isMobile = Vm.$cjc.browser.versions.mobile
            if (window.WebViewJavascriptBridge) {
                // 调用JS-API登录
                await Vm.$cjc.apiLogin({
                    appKey: '0000',
                    success: (data) => { // 执行成功回调
                        Vm.$cjc.cjcLog('getlogin success' + data)
                    },
                    fail: (data) => { // 执行失败回调 只要是失败 不论是执行失败还是返回值是失败都会进fail
                        Vm.$cjc.cjcLog('getlogin fail' + data)
                    }
                })
                Vm.$cjc.hiddenNavigation()
                let userInfo = await Vm.$cjc.apiGetUserInfor()
                Vm.$store.commit('SET_userInfo', userInfo.result)
                await Vm.$store.dispatch('saveUserInfo', userInfo.result)
            } else {
                // let userInfo = await Vm.$store.dispatch('getUserInfo', {
                //     'key_id': '41401432',
                //     'type': 1
                // })
                let userInfo = await Vm.$store.dispatch('getUserInfo', {
                    key_id: keyId,
                    type
                })
                Vm.$store.commit('SET_userInfo', userInfo.data.result)
                await Vm.$store.dispatch('saveUserInfo', userInfo.data.result)
            }

            // // 测试账号登录
            // let userLogin = await Vm.$store.dispatch('userlogin', {
            //     'username': '41401427',
            //     'password': '416321',
            //     'type': 1
            // })
            // Vm.$store.commit('SET_userInfo', userLogin.data.result)
            // await Vm.$store.dispatch('saveUserInfo', userLogin.data.result)
        }
        /**
         * 设置校历周次
         */
        async function setTimeList() {
            let TimeList = await Vm.$store.dispatch('getTimeList')
            Vm.$store.commit('SET_TimeList', TimeList.result)
        }
        /**
         * 设置当前校历周
         */
        async function setSchoolTime() {
            let ZC = sessionStorage.getItem('ZC')
            if (ZC === '' || ZC === '0' || !State.SchoolTime) { // 在周次为空或者SchoolTime为空的时候执行赋值调用
                let TimeList = await Vm.$store.dispatch('getSchoolTime')
                Vm.$store.commit('SET_SchoolTime', TimeList.result)
                sessionStorage.setItem('ZC', State.SchoolTime.ZC)
                Vm.zc = sessionStorage.getItem('ZC')
            }
        }
        /**
         * 设置课程列表
         */
        async function setSyllabusList() {
            if (Vm.oldRouter === null || Vm.oldRouter === 'home') { // 如果是从二级页面返回不会执行该方法
                let ZC = sessionStorage.getItem('ZC')
                let SyllabusList = await Vm.$store.dispatch('getSyllabusList', {
                    xh: State.userInfo.XH,
                    xn: State.SchoolTime.XN,
                    xq: State.SchoolTime.XQ,
                    zc: ZC
                })
                if (SyllabusList.status === 0) {
                    Vm.$store.commit('SET_SyllabusList', SyllabusList.result)
                } else {
                    Vm.$store.commit('SET_SyllabusList', 'noData')
                    Message.warning(SyllabusList.msg)
                }
            }
        }
        /**
         * 执行步骤
         */
        async function step() {
            try {
                await setUserInfo()
                await setTimeList()
                await setSchoolTime()
                await setSyllabusList()
            } catch (Error) {
                Modal.error({
                    title: '错误',
                    content: '抱歉，发生了错误，请联系管理员<br>' + Error
                })
            }
        }

        step()
    }
}
</script>

<style lang="less">
    .home {
        height: 100%;
    }
    .dropdown-text{
        overflow-y: scroll;
        max-height: 300px;
        text-align: center;
    }
</style>



